.team {
  background-color: #f4f4f9;
  justify-content: space-between;
  gap: 3rem;
  padding: 3rem 5rem 1rem;
  display: flex;
}

.team__text {
  align-self: center;
}

.team__text h1 {
  font-weight: 600;
  display: block;
}

.team__text span {
  display: block;
}

.team__text p {
  width: 90%;
  font-size: .7rem;
  font-weight: 500;
}

.team img {
  width: 30rem;
}

@media screen and (max-width: 650px) {
  .team {
    flex-direction: column;
    padding: 2rem;
  }

  .team__text {
    align-self: start;
  }

  .team img {
    width: 80%;
    height: auto;
    max-width: 90%;
    align-self: end;
  }
}

.people__1, .people__2 {
  width: 80%;
  justify-content: space-between;
  gap: 1rem;
  margin: 0 auto;
  padding: 2rem 5rem;
  display: flex;
}

.people__1 img, .people__2 img {
  width: 16rem;
  height: 18rem;
}

.people__bio1 {
  flex-basis: 50%;
}

.people__bio2 {
  text-align: right;
  flex-basis: 50%;
}

.people__bar {
  height: 2px;
  background-color: #000;
  margin: 0 5rem;
}

.people h2 {
  font-weight: 600;
}

.people p {
  font-size: .75rem;
  font-weight: 500;
}

@media screen and (max-width: 650px) {
  .people__1, .people__2 {
    width: 100%;
    padding: 2rem;
  }

  .people__bar {
    margin: 0 2rem;
  }

  .people img {
    width: 50%;
    height: 13rem;
  }
}

